@import "../../theme.scss";

.player{
  float: left;
  margin-right: 10px;
  width: 70%;
  min-height: 350px;
  font-size: 25px;
  max-height: 100vh;
  border-bottom: 1px solid rgba(128, 128, 128, 0.541);
  padding-bottom: 10px;
  overflow-y: scroll;
  background-color: #fff;
  z-index: 3;
  
    .frame-wrapper {
        position: relative;
        padding-bottom: 56.25%; /* 16:9 */
        height: 0;
        background-color: rgb(235, 235, 235);
        margin-bottom: 10px;
    }
    .frame-wrapper iframe {
        background-color: $gray;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    @media (max-width: $break-tablet){
        width: 100%;
        float: unset;
    }

    @media (max-width: $break-mobile){
        overflow-y: unset;
    }
}

.fold-player{
    min-height: 20px;
    width: 100%;
    float: unset;
    overflow-y: unset;
    padding: unset;

    .player-outer-wrapper{
        display: none;
    }
}

@media (max-width: $break-mobile){
    .fixed{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%
    }
}
